// import { NgbModal, NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';
import { NgForm } from '@angular/forms';
import { Component, OnInit, ViewChild } from '@angular/core';
import { UtilsService } from 'src/app/services/utils.service';
import { LocationService } from 'src/app/services/location.service';
import { BackServiceLocation, BackServiceParking } from 'src/app/models/back-service.model';

@Component({
  selector: 'app-edit-ubicaciones',
  templateUrl: './edit-ubicaciones.component.html',
  styleUrls: ['./edit-ubicaciones.component.css']
})
export class EditUbicacionesComponent implements OnInit {
  @ViewChild('form') form!: NgForm;
  
  public active: number = 1;
  public eventID: number = 0;
  public editType: string = '';
  public register_id: number = 0;
  public loading: boolean = false;
  public listLocation: any[] = [];
  public disabled: boolean = false;
  public parkingData = {} as BackServiceParking;
  public locationData = {} as BackServiceLocation;

  constructor(
    private utilsService: UtilsService,
    private locationService: LocationService
  ) { this.eventID = Number(sessionStorage.getItem('EventID')) || 0 }

  changeTab(number: number){
    const element = document.getElementById('nav_' + number)!;
    // element.classList.remove('active');
    element?.classList.add('active');
    console.log(number);
  }
  
  // changeTab2(number: number){
  //   const tabEl = document.querySelector('button[data-bs-toggle="tab"]')
  //   tabEl.addEventListener('shown.bs.tab', event => {
  //     event.target // newly activated tab
  //     event.relatedTarget // previous active tab
  //   })

  // }



  async ngOnInit(): Promise<void> {
    // REFERENCIA EDITAR AGENDA
    const token: string | undefined = sessionStorage.getItem('Token')?.toString();

    if (token) {
      if (this.eventID > 0) {
        await this.getLocationByEvent(this.eventID);

        this.register_id = await this.utilsService.getParam('ID') || 0;
        this.editType = await this.utilsService.getParam('EDIT') || '';

        if (this.editType != '' && this.register_id > 0) {
          await this.getOne(this.register_id);
        }
      } else {
        this.utilsService.dialog('ERROR', 'Alerta', '¡No tienes seleccionado ningun evento!');
        this.goToPage('/admin/events/calendario-eventos');
      }
    }
    else {
      this.utilsService.dialog('ERROR', 'Error', '¡Inicia sesión pro favor!');
      this.goToPage('/login');
    }
  }

  goToPage(pageName: string) {
    this.utilsService.goToPage(pageName);
  }

  validation_msg(name: string): boolean {
    if (this.form && this.form.controls[name]) {
      if (
        (this.form.controls[name].errors || this.form.controls[name].invalid)
        &&
        (this.form.controls[name].dirty || this.form.controls[name].touched)
      ) {
        return true;
      } else {
        return false;
      }
    } else {
      return false;
    }
  }

  getLocationByEvent(event_id: number) {
    this.loading = true;
    return this.locationService.getLocationByEvent(event_id).then(
      (response) => {
        this.listLocation = response.result.eventPlaces.map((e: any) => {
          return {
            id: e.id,
            name: e.title
          }
        })
        // console.log(this.listLocation)
      },
      error => {
        console.log(error);
      }).finally(() => {
        this.loading = false;
      })
  }

  getOne(register_id: number) {
    this.loading = true;

    if (this.editType === 'location') {
      this.active = 1;
      this.locationService.getOneLocation(register_id).then(
        (response) => {
          this.locationData.id = response.result?.id;
          this.locationData.eventId = response.result?.eventId;
          this.locationData.direccion = response.result?.direccion;
          this.locationData.nombreUbicacion = response.result?.nombreUbicacion;
          this.locationData.enlaceUbicacion = response.result?.enlaceUbicacion;
          this.locationData.horaFin = response.result?.fechaHoraFin.substr(11, 5);
          this.locationData.horaInicio = response.result?.fechaHoraInicio.substr(11, 5);

          if (response.result.fechaHoraInicio) {
            const tempStar = (response.result.fechaHoraInicio.substr(0, 10)).split('-');
            this.locationData.fechaInicio = { day: Number(tempStar[2]), month: Number(tempStar[1]), year: Number(tempStar[0]) };
          }

          if (response.result.fechaHoraFin) {
            const tempEnd = (response.result.fechaHoraFin.substr(0, 10)).split('-');
            this.locationData.fechaFin = { day: Number(tempEnd[2]), month: Number(tempEnd[1]), year: Number(tempEnd[0]) };
          }
          // console.log(response.result)
          // console.log(this.locationData)
        },
        error => {
          console.log(error);
          this.utilsService.dialog('ERROR', 'Error', '¡Error al cargar los datos!');
          this.goToPage('/admin/events/admin-agenda');
        }).finally(() => {
          this.loading = false
        })
    }
    else if (this.editType === 'parking') {
      this.active = 2;
      this.locationService.getOneParking(register_id).then(
        (response) => {
          this.parkingData.id = response.result?.id;
          this.parkingData.direccion = response.result?.direccion;
          this.parkingData.locationId = response.result?.locationId;
          this.parkingData.nombreParqueo = response.result?.nombreParqueo;
          this.parkingData.enlaceUbicacion = response.result?.enlaceUbicacion;
          this.parkingData.horaFin = response.result?.fechaHoraFin.substr(11, 5);
          this.parkingData.horaInicio = response.result?.fechaHoraInicio.substr(11, 5);

          if (response.result.fechaHoraInicio) {
            const tempStar = (response.result.fechaHoraInicio.substr(0, 10)).split('-');
            this.parkingData.fechaInicio = { day: Number(tempStar[2]), month: Number(tempStar[1]), year: Number(tempStar[0]) };
          }

          if (response.result.fechaHoraFin) {
            const tempEnd = (response.result.fechaHoraFin.substr(0, 10)).split('-');
            this.parkingData.fechaFin = { day: Number(tempEnd[2]), month: Number(tempEnd[1]), year: Number(tempEnd[0]) };
          }
          console.log(response.result)
          console.log(this.parkingData)
        },
        error => {
          console.log(error);
          this.utilsService.dialog('ERROR', 'Error', '¡Error al cargar los datos!');
          this.goToPage('/admin/events/admin-agenda');
        }).finally(() => {
          this.loading = false
        })
    }
  }

  processData(type: number) {
    if (this.register_id > 0) {
      this.update(type)
    } else {
      this.create(type);
    }
  }

  create(type: number) {
    this.loading = true;
    this.disabled = true;

    if (type === 1) {
      this.locationData.eventId = this.eventID;
      this.locationData.title = this.locationData.nombreUbicacion;

      if (this.locationData.fechaFin) {
        this.locationData.fechaHoraFin = `${this.locationData.fechaFin.year}-${this.locationData.fechaFin.month}-${this.locationData.fechaFin.day} ${this.locationData.horaFin}`;
      }
      if (this.locationData.fechaInicio) {
        this.locationData.fechaHoraInicio = `${this.locationData.fechaInicio.year}-${this.locationData.fechaInicio.month}-${this.locationData.fechaInicio.day} ${this.locationData.horaInicio}`;
      }

      this.locationService.createLocationToEvent(this.locationData).then(
        (response) => {
          this.utilsService.dialog('SUCCESS', 'Éxito', '¡Ubicación creada!');
        },
        error => {
          console.log(error);
          this.utilsService.dialog('ERROR', 'Error', '¡Error al crear uicación!');
        }).finally(() => {
          this.loading = false
          this.disabled = false;
          this.goToPage('/admin/events/admin-ubicaciones');
        })

    } else {
      if (this.parkingData.fechaFin) {
        this.parkingData.fechaHoraFin = `${this.parkingData.fechaFin.year}-${this.parkingData.fechaFin.month}-${this.parkingData.fechaFin.day} ${this.parkingData.horaFin}`;
      }
      if (this.parkingData.fechaInicio) {
        this.parkingData.fechaHoraInicio = `${this.parkingData.fechaInicio.year}-${this.parkingData.fechaInicio.month}-${this.parkingData.fechaInicio.day} ${this.parkingData.horaInicio}`;
      }

      console.log('parking ', this.parkingData)
      this.locationService.createLocationToEvent(this.parkingData).then(
        (response) => {
          this.utilsService.dialog('SUCCESS', 'Éxito', '¡Parqueo creado!');
        },
        error => {
          console.log(error);
          this.utilsService.dialog('ERROR', 'Error', '¡Error al crear parqueo!');
        }).finally(() => {
          this.loading = false
          this.disabled = false;
          this.goToPage('/admin/events/admin-ubicaciones');
        })
    }
  }

  update(type: number) {
    this.loading = true;
    this.disabled = true;

    if (type === 1) {
      this.locationData.eventId = this.eventID;
      this.locationData.title = this.locationData.nombreUbicacion;

      if (this.locationData.fechaFin) { // this.locationData.fechaHoraFin
        this.locationData.fechaHoraFin = `${this.locationData.fechaFin.year}-${this.locationData.fechaFin.month}-${this.locationData.fechaFin.day} ${this.locationData.horaFin}`;
      }
      if (this.locationData.fechaInicio) {
        this.locationData.fechaHoraInicio = `${this.locationData.fechaInicio.year}-${this.locationData.fechaInicio.month}-${this.locationData.fechaInicio.day} ${this.locationData.horaInicio}`;
      }

      console.log(this.locationData)
      this.locationService.updateLocationByEvent(this.locationData.id, this.locationData).then(
        (response) => {
          this.utilsService.dialog('SUCCESS', 'Éxito', '¡Ubicación actualizada!');
        },
        error => {
          console.log(error);
          this.utilsService.dialog('ERROR', 'Error', '¡Error al actualizar ubicación!');
        }).finally(() => {
          this.loading = false
          this.disabled = false;
          this.goToPage('/admin/events/admin-ubicaciones');
        })

    } else {
      if (this.parkingData.fechaFin) { // this.parkingData.fechaHoraFin
        this.parkingData.fechaHoraFin = `${this.parkingData.fechaFin.year}-${this.parkingData.fechaFin.month}-${this.parkingData.fechaFin.day} ${this.parkingData.horaFin}`;
      }
      if (this.parkingData.fechaInicio) { // this.parkingData.fechaHoraInicio
        this.parkingData.fechaHoraInicio = `${this.parkingData.fechaInicio.year}-${this.parkingData.fechaInicio.month}-${this.parkingData.fechaInicio.day} ${this.parkingData.horaInicio}`;
      }

      console.log('parking ', this.parkingData)
      this.locationService.updateParkingByLocation(this.parkingData.id, this.parkingData).then(
        (response) => {
          this.utilsService.dialog('SUCCESS', 'Éxito', '¡Parqueo actualizado!');
        },
        error => {
          console.log(error);
          this.utilsService.dialog('ERROR', 'Error', '¡Error al actualizar parqueo!');
        }).finally(() => {
          this.loading = false
          this.disabled = false;
          this.goToPage('/admin/events/admin-ubicaciones');
        })
    }
  }
}
